<template>
  <div class="Rahmen">
    <div class="welcome">
      <h2>Willkommen</h2>
    </div>
    <div class="welcome-text">
      <h5 style=margin:0>
        Planen Sie mit unserem Konfigurator einfach und schnell Ihre
        individuelle Containertreppenl&oumlsung
      </h5>
    </div>

    <div class="hilfetext">
      <span id="span">So klappt's</span>
      <b-button pill id="popover-help-button"
        ><v-icon id="popover-help" name="question-circle"
      /></b-button>
      <b-popover
        ref="popover"
        target="popover-help-button"
        triggers="hover"
        placement="top"
      >
        Wählen Sie bitte aus wie viele Container sich in Ihrer Containeranlage
        in Höhe und Breite befinden.
        <img src="@/assets/helpp1.gif" width="440" />
      </b-popover>
    </div>
    <div unselectable="on" class="rendercanvas">
      <canvas unselectable="on" id="renderCanvas"></canvas>
    </div>
  </div>
