  components: {
    'v-icon': Icon
  },
  data: function () {
    return {
      farbe: null,
      heigth: 0,
      width: 0,
      canvas: null,
      engine: null,
      scene: null,
      mainCamera: null,
      light1: null,
      assetsManager: null,
      tasks: [],
      loadedMesh: [],
      originalColor: [],
      enableChangeColorTrigger: true,
      colorContainer: null
    }
