  methods: {
    changeColorTrigger (index, pointOver) {
      var heigthTemp, widthTemp
      if (this.enableChangeColorTrigger == true) {
        if (index >= 9 && index < 18) {
          heigthTemp = 2
          widthTemp = index - 8
        } else if (index >= 18) {
          heigthTemp = 3
          widthTemp = index - 17
        } else {
          heigthTemp = 1
          widthTemp = index + 1
        }
        if (pointOver == true) {
          var l
          for (var k = 0; k < heigthTemp; k++) {
            for (var m = 0; m < widthTemp; m++) {
              /* for (var l = (k * 9 + m) * 5; l < (k * 9 + m) * 5 + 5; l++) {
                this.loadedMesh[l].material = farbe;
              } */
              l = k * 9 + m
              this.loadedMesh[l * 5].material = this.farbe
              this.loadedMesh[l * 5 + 1].material = this.farbe
            }
          }
        } else {
          for (var i = 0; i < 135; i = i + 5) {
            this.loadedMesh[i].material = this.originalColor[0]
            this.loadedMesh[i + 1].material = this.originalColor[1]
            for (var k = 2; k < 5; k++) {
              this.loadedMesh[i + k] = this.originalColor[k]
            }
          }
        }
      }
    },
    colorContainerMethod (index) {
      this.enableChangeColorTrigger = false
      if (index >= 9 && index < 18) {
        this.heigth = 2
        this.width = index - 8
      } else if (index >= 18) {
        this.heigth = 3
        this.width = index - 17
      } else {
        this.heigth = 1
        this.width = index + 1
      }
      EventBus.$emit('emited', { heigth: this.heigth, width: this.width })
      for (var i = 0; i < 135; i = i + 5) {
        for (var k = 0; k < 5; k++) {
          this.loadedMesh[i + k].material = this.originalColor[k]
        }
      }
      var l
      for (var k = 0; k < this.heigth; k++) {
        for (var m = 0; m < this.width; m++) {
          /* for (var l = (k * 9 + m) * 5; l < (k * 9 + m) * 5 + 5; l++) {
            this.loadedMesh[l].material = farbe;
          } */
