import Vue from 'vue'
import Router from 'vue-router'

import Page1 from './views/Page1'
import Page2 from './views/Page2'
import Page3 from './views/Page3'
import Page4 from './views/Page4'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/page1',
      name: '/page1',
      meta: { layout: 'SplitscreenVertical' }, // not necessary for current dynamic layout
      component: Page1
    },
    {
      path: '/page2',
      name: '/page2',
      meta: { layout: 'SplitscreenVertical' }, // not necessary for current dynamic layout
      component: Page2
    },
    {
      path: '/page3',
      name: '/page3',
      meta: { layout: 'SplitscreenVertical' }, // not necessary for current dynamic layout
      component: Page3
    },
    {
      path: '/page4',
      name: '/page4',
      meta: { layout: 'SplitscreenVertical' }, // not necessary for current dynamic layout
      component: Page4
    }
  ]
})
