<template>
  <div class="Hauptcontainer">

        <!-- Auswahl des Containertyps. Buttons werden erst aktiviert, sobald ein Container aus dem Feld ausgewählt wird. Initialisierung, wenn Button geklickt. -->
        <div class="containertyp">
          <b>Containertyp:</b>
        </div>
        <div class="buerostirn">
          <button id="buerostirn" class="btn btn-outline-secondary"
                  v-on:click="chosetype('buerostirn')"
                  v-bind:class="{'activeBtn': containertyp_selected == 'buerostirn'}">
            B&uumlro; T&uumlr stirnseitig
          </button>
        </div>
        <div class="buerolaengs">
          <button id="buerolaengs"
                  class="btn btn-outline-secondary"
                  v-on:click="chosetype('buerolaengs')"
                  v-bind:class="{'activeBtn': containertyp_selected == 'buerolaengs'}">
            B&uumlro;
            T&uumlr l&aumlngsseitig (Nur an äußeren Containern)
          </button>
        </div>
        <div class="lagervorne">
          <button id="lagervorne"
                  class="btn btn-outline-secondary"
                  v-on:click="chosetype('lagervorne')"
                  v-bind:class="{'activeBtn': containertyp_selected == 'lagervorne'}">
            Lager;
            Fl&uumlgeltor vorne (nur EG, nur in Höhe 2,60m)
          </button>
        </div>
        <div class="lagerhinten">
          <button id="lagerhinten"
                  class="btn btn-outline-secondary"
                  v-on:click="chosetype('lagerhinten')"
                  v-bind:class="{'activeBtn': containertyp_selected == 'lagerhinten'}">
            Lager; Fl&uumlgeltor hinten (nur EG, nur in Höhe 2,60m)
          </button>
        </div>

        <!-- Auswahl der Abmaße der Container. Initialisierung, wenn Button geklickt.-->
        <div class="abmaße">
          <b>Abmaße Container (für alle Container gleich):</b>
        </div>
        <div class="breite">
          <b>Breite:</b>
        </div>
        <div class="zweivierdrei">
          <button class="btn btn-outline-secondary"
                  v-on:click="setWidthcont(2.43)"
                  v-bind:class="{'activeBtn': widthcont == 2.43}">
            2,43
          </button>
        </div>
        <div class="drei">
          <button class="btn btn-outline-secondary"
                  v-on:click="setWidthcont(3.00)"
                  v-bind:class="{'activeBtn': widthcont == 3.00}">
            3,00
          </button>
        </div>
        <div class="hoehe">
          <b>Höhe:</b>
        </div>
        <div class="zweisechs">
          <button class="btn btn-outline-secondary"
                  v-on:click="setHeightcont(2.60)"
                  v-bind:class="{'activeBtn': heightcont == 2.60}">
            2,60
          </button>
        </div>
        <div class="zweiacht">
          <button id="zweiacht" class="btn btn-outline-secondary"
                  v-on:click="setHeightcont(2.80)"
                  v-bind:class="{'activeBtn': heightcont == 2.80}">
            2,80
          </button>
        </div>
        <div class="zweineunsechs">
          <button id="zweineunsechs" class="btn btn-outline-secondary"
                  v-on:click="setHeightcont(2.96)"
                  v-bind:class="{'activeBtn': heightcont == 2.96}">
            2,96
          </button>
        </div>
        <div class="laenge">
          <b>Länge:</b>
        </div>
        <div class="sechsnullfünf">
          <button class="btn btn-outline-secondary"
                  v-on:click="setLengthcont(6.05)"
                  v-bind:class="{'activeBtn': lengthcont == 6.05}">
            6,05
          </button>
        </div>
      <b-button class="navigation1" v-on:click="weiter()">weiter</b-button>
      <b-button class="navigation2" v-on:click="zurueck()">zurück</b-button>
  </div>
</template>

<script>
import { EventBus } from '@/main.js'

export default {
  data: function () {
    return {
      heigth: 0,
      width: 0,
      containerrechts: '',
      containerlinks: '',
      heightcont: 2.6,
      widthcont: 2.43,
      lengthcont: 6.05,
      nr: null,
      id: [],
      containertyp_selected: '',
      containertypArr: [],
      lagervorneenthalten: false
    }
  },

  methods: {
    // Vue-Wechsel zur P1
    zurueck: function () {
      EventBus.$emit('p2top1', false)
      this.heightcont = 2.6
      this.widthcont = 2.43
      this.lengthcont = 6.05
      this.height = 0
      this.width = 0
    },
    // Vue-Wechsel zu P3
    weiter: function () {
      const self = this
      EventBus.$emit('p2top3', {
        heigthCont: self.heightcont,
        widthCont: self.widthcont,
        containertyp: self.containertypArr
      })
    },
    setButtons (indexArr) {
      var i = 0

      document.getElementById('lagerhinten').disabled = false
      document.getElementById('lagervorne').disabled = false
      document.getElementById('buerolaengs').disabled = false
      while (indexArr[i] != null) {
        var index = indexArr[i]
        if (index >= 9 && index < 18 && (index == 9 || index - 8 - this.width == 0)) {
          document.getElementById('lagerhinten').disabled = true
          document.getElementById('lagervorne').disabled = true
        } else if (index >= 9 && index < 18 && (index != 9 && index + 1 - this.width != 0)) {
          document.getElementById('lagerhinten').disabled = true
          document.getElementById('lagervorne').disabled = true
          document.getElementById('buerolaengs').disabled = true
        } else if ((index < 9 && index + 1 - this.width == 0) || index == 0) {

        } else if (index < 9) {
          document.getElementById('buerolaengs').disabled = true
        }
        i++
      }
    },
    setWidthcont (width) {
      this.widthcont = width
    },
    setHeightcont (heigth) {
      this.heightcont = heigth
    },
    setLengthcont (length) {
      this.lengthcont = length
    },

    // wenn Containertyp ausgewählt, übergabe nach P2 links
    chosetype (containertyp) {
      EventBus
        .$emit('containertypchosen', { containertyp: containertyp })
    }
  },
  created () {
    for (var index = 0; index < 27; index++) {
      this.containertypArr[index] = 'buerostirn'
    }
    document.getElementById('lagerhinten').disabled = false
    document.getElementById('lagervorne').disabled = false
    document.getElementById('buerolaengs').disabled = false
  },
  // bei Update aktualisierung der Daten auf P2 Links
  mounted () {
    // Datenempfang von P1
    const self = this
    EventBus.$on('containertypUpdate', function ({ containertypArr }) {
      self.containertypArr = containertypArr
      var i = 0
      var lagervorneenthalten = false
      while (self.containertypArr[i] != null) {
        if (self.containertypArr[i] == 'lagervorne' || self.containertypArr[i] == 'lagerhinten') {
          lagervorneenthalten = true
          document.getElementById('zweiacht').disabled = true
          document.getElementById('zweineunsechs').disabled = true
          self.heightcont = 2.6
          break
        }
        i++
      }
      if (lagervorneenthalten == false) {
        document.getElementById('zweiacht').disabled = false
        document.getElementById('zweineunsechs').disabled = false
      }
    })
    EventBus.$on('p1top2right', function ({ heigth, width }) {
      document.getElementById('lagerhinten').disabled = false
      document.getElementById('lagervorne').disabled = false
      document.getElementById('buerolaengs').disabled = false
      self.heigth = heigth
      self.width = width
      if (heigth == 3) {
        document.getElementById('zweineunsechs').disabled = true
      }
    })
    // Datenempfang, wenn Container links ausgewählt
    EventBus.$on('containerclicked1', function ({ id }) {
      if (id != null) {
        self.id = id
        self.setButtons(id)
      } else {
        this.BueroLaengs = true
        this.LagerVorne = true
        this.LagerHinten = true
      }
    })
    EventBus.$emit('p2rightrdy')
  }
}
</script>

<style>
.Hauptcontainer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: repeat(10, 0.5fr);
  background: #e6e7e8;
  border-left: 2px solid black;
  padding-right: 5%;
  padding-left: 5%;
  height: 100%;
}
.containertyp {
    padding-top: 8%;
    grid-column: 1 / 2;
    grid-row: 1 / 2;
}
.buerostirn {
    grid-column: 1 / 6;
    grid-row: 2 / 3;
}
.buerolaengs {
    grid-column: 1 / 6;
    grid-row: 3 / 4;
}
.lagervorne {
    grid-column: 1 / 6;
    grid-row: 4 / 5;
}
.lagerhinten {
    grid-column: 1 / 6;
    grid-row: 5 / 6;
}
.abmaße {
    grid-column: 1 / 6;
    grid-row: 6 / 7;
}
.breite {
    grid-column: 1 / 2;
    grid-row: 7 / 8;
}
.zweivierdrei {
    grid-column: 2 / 3;
    grid-row: 7 / 8;
}
.drei {
    grid-column: 3 / 4;
    grid-row: 7 / 8;
}
.hoehe {
    grid-column: 1 / 2;
    grid-row: 8 / 9;
}
.zweisechs {
    grid-column: 2 / 3;
    grid-row: 8 / 9;
}
.zweiacht {
    grid-column: 3 / 4;
    grid-row: 8 / 9;
}
.zweineunsechs {
    grid-column: 4 / 5;
    grid-row: 8 / 9;
}
.laenge {
    grid-column: 1 / 2;
    grid-row: 9 / 10;
}
.sechsnullfünf {
    grid-column: 2 / 3;
    grid-row: 9 / 10;
}
#buerolaengs {
    width: 100%;
}
#lagerhinten {
    width: 100%;
}
#lagervorne {
    width: 100%;
}
#buerostirn {
    width: 100%;
}
button {
  position: relative;
  height: 40px;
  width: 90%;
  text-align: center;
  padding: 0px;
}

.activeBtn {
  border: 2px solid black;
}

.navigation1 {
  grid-column: 4 / 5;
  grid-row: 10 / 11;
  color: whitesmoke;
  background-color: #003f84;
  width: 90%;

}

.navigation2 {
  grid-column: 1 / 2;
  grid-row: 10 / 11;
  color: whitesmoke;
  background-color: #003f84;
  width: 90%;
}
@media only screen and (max-width: 1150px){
    .Hauptcontainer {
      padding-bottom: 2%;
      box-shadow: 1px 1px 5px #000000;
      border-bottom: #283282;
      border-left: 0;
      background: #F0F1F2;
    }
}
</style>
