<template>
  <div :key="rerender">
    <!--
  Alle PageX.vue werden in die App geladen, so wie alle Komponenten in ihre PageX.vue
  Parent & Child Concept
    -->
    <keep-alive>
      <component :is="dynamicComponent"></component>
    </keep-alive>
    <!-- <Page1 v-show="p1visible == true" />
  <Page2 v-show="p2visible == true" />
  <Page3 v-show="p3visible == true" />
    <Page4 v-show="p4visible == true" />-->
  </div>
</template>

<script>
// Der Event Handler zwecks Page Wechsel und alle verwendeten Custom Komponenten werden importiert
import { EventBus } from '@/main.js'
import Page1 from '@/views/Page1.vue'
import Page2 from '@/views/Page2.vue'
import Page3 from '@/views/Page3.vue'
import Page4 from '@/views/Page4.vue'

export default {
  /* Die Komponenten werden registriert um für die Single Page Application
          später zur Verfügun zu stehen, das selbe KOnzept findet sich auf jeder Ebene:
          Hier die PageX.vue(s) in der App.vue in den PageX.vue wiederum die Links und Rechts.vue usw
          entlang der Hierarchie der Architektur.
         */
  components: {
    Page1,
    Page2,
    Page3,
    Page4
  },

  // Einstellung welche Page beim Start sichtbar ist
  data: function () {
    return {
      p1visible: true,
      p2visible: false,
      p3visible: false,
      p4visible: false,
      p1loaded: false,
      p2loaded: false,
      p3loaded: false,
      p4loaded: false,
      rerender: 0,
      width: 0,
      heigth: 0,
      heigthCont: null,
      widthCont: null,
      containertyp: [],
      modulBezRechts: null,
      modulBezLinks: null,
      modBez6B: null,
      modBez6Lv: null,
      BueroAnz: null,
      LagerVAnz: null,
      gelaender: 0,
      treppe: 0,
      laufsteg: 0,
      modBez6Lv_2Stützen: null,
      LagerVAnz_2Stützen: null,
      output: null,
      LagerHAnz: null,
      modBez6Lh: null,
    }
  },
  /* Eventbus mounted Funktionen die je nach Buttonevent in den einzelnen Views eine neue
          "Ansicht" aktivieren
         */
  mounted () {
    const self = this
    // Daten können erst übergeben werden, wenn Seite vollständig geladen
    EventBus.$on("output", function({output}) {
      self.output = output
    }),
    EventBus.$on('p2leftrdy', function () {
      EventBus.$emit('p1top2left', {
        heigth: self.heigth,
        width: self.width
      })
      self.p2loaded = true
    })
    EventBus.$on('p2rightrdy', function () {
      EventBus.$emit('p1top2right', {
        heigth: self.heigth,
        width: self.width
      })
      self.p2loaded = true
    })
    EventBus.$on('p3leftrdy', function () {
      EventBus.$emit('appToP3left', {
        width: self.width,
        heigth: self.heigth,
        containertyp: self.containertyp
      })
      self.p3loaded = true
    })
    EventBus.$on('p3rigthrdy', function () {
      EventBus.$emit('appToP3rigth', {
        width: self.width,
        heigth: self.heigth,
        heigthCont: self.heigthCont,
        widthCont: self.widthCont,
        containertyp: self.containertyp
      })
      self.p3loaded = true
    })
    EventBus.$on('p4rdy', function () {
      EventBus.$emit('p3righttop4', {
        modulBezRechts: self.modulBezRechts,
        modulBezLinks: self.modulBezLinks,
        modBez6B: self.modBez6B,
        modBez6Lv: self.modBez6Lv,
        modBez6Lh: self.modBez6Lh,
        BueroAnz: self.BueroAnz,
        LagerVAnz: self.LagerVAnz,
        LagerHAnz: self.LagerHAnz,
        gelaender: self.gelaender,
        treppe: self.treppe,
        laufsteg: self.laufsteg,
        heightCont: self.heigthCont,
        widthCont: self.widthCont,
        modBez6Lv_2Stützen: self.modBez6Lv_2Stützen,
        LagerVAnz_2Stützen: self.LagerVAnz_2Stützen,
        output: self.output
      })
      self.p4loaded = true
    })
    // BusEvent der Weiter-Funktionen
    EventBus.$on('p1top2', function ({ heigth, width }) {
      self.p1visible = false
      self.p2visible = true
      self.p3visible = false
      self.p4visible = false
      if (self.heigth != heigth || self.width != width) {
        self.heigth = heigth
        self.width = width
        EventBus.$emit('p1top2left', {
          heigth: self.heigth,
          width: self.width
        })
        EventBus.$emit('p1top2right', {
          heigth: self.heigth,
          width: self.width
        })
        if (self.p2loaded == true) {
          self.rerender++
        }
      }
    })
    EventBus.$on('p2top1', function () {
      self.p1visible = true
      self.p2visible = false
      self.p3visible = false
      self.p4visible = false
    }),
    EventBus.$on('p3top2', function () {
      self.p1visible = false
      self.p2visible = true
      self.p3visible = false
      self.p4visible = false
    }),
    EventBus.$on('p2top3', function ({ heigthCont, widthCont, containertyp }) {
      var equal = true
      var i = 0
      self.heigthCont = heigthCont,
      self.widthCont = widthCont
      while (containertyp[i] != null) {
        if (self.containertyp[i] != containertyp[i]) {
          equal = false
          break
        }
        i++
      }
      if (equal == false) {
        for (var k = 0; k < 27; k++) {
          self.$set(self.containertyp, k, containertyp[k])
        }
        EventBus.$emit('appToP3left', {
          width: self.width,
          heigth: self.heigth,
          heigthCont: self.heigthCont,
          widthCont: self.widthCont,
          containertyp: self.containertyp
        })
        EventBus.$emit('appToP3rigth', {
          width: self.width,
          heigth: self.heigth,
          heigthCont: self.heigthCont,
          widthCont: self.widthCont,
          containertyp: self.containertyp
        })
        if (self.p3loaded == true) {
          self.rerender++
        }
      }

      self.p1visible = false
      self.p2visible = false
      self.p3visible = true
      self.p4visible = false
    })
    EventBus.$on('p4top3', function () {
      self.p1visible = false
      self.p2visible = false
      self.p3visible = true
      self.p4visible = false
    }),
    EventBus.$on('indAnfrage', function () {
      self.p1visible = false,
      self.p2visible = false,
      self.p3visible = false,
      self.p4visible = true
    })
    EventBus.$on('p3top4', function ({ modulBezRechts, modulBezLinks, modBez6B, modBez6Lv, modBez6Lh, BueroAnz,
      LagerVAnz, LagerHAnz, gelaender, treppe, laufsteg, modBez6Lv_2Stützen, LagerVAnz_2Stützen }) {
      self.p1visible = false
      self.p2visible = false
      self.p3visible = false
      self.p4visible = true
      self.modulBezRechts = modulBezRechts,
      self.modulBezLinks = modulBezLinks,
      self.modBez6B = modBez6B,
      self.modBez6Lv = modBez6Lv,
      self.modBez6Lh = modBez6Lh,
      self.BueroAnz = BueroAnz,
      self.LagerVAnz = LagerVAnz,
      self.LagerHAnz = LagerHAnz,
      self.gelaender = gelaender,
      self.treppe = treppe,
      self.laufsteg = laufsteg,
      self.modBez6Lv_2Stützen = modBez6Lv_2Stützen,
      self.LagerVAnz_2Stützen = LagerVAnz_2Stützen
      if (self.p4loaded == true) {
        self.rerender++
      }
    }),
    EventBus.$on("image", function(output){
      self.output = output
    })
  },
  computed: {
    dynamicComponent () {
      if (this.p1visible) {
        return 'Page1'
      } else if (this.p2visible) {
        return 'Page2'
      } else if (this.p3visible) {
        return 'Page3'
      } else if (this.p4visible) {
        return 'Page4'
      }
    }
  }
}
</script>

<style >
  keep-alive{
    overflow: hidden;
  }
</style>
