<template>
  <div id="body" class="body">
    <div class="container">
      <div class="wrapper">
        <div id="left" class="out-list">
          <h2 class="parts-header">Teileliste</h2>
          <b-table
            class="partslist"
            striped
            hover
            :items="items"
            :fields="fields"
          >
          </b-table>
        </div>
        <div id="right-wrapper" class="right-wrapper">
          <div id="right" class="customer-input">
            <b-form id="form"  v-if="show">
              <h2 id="Anfrage-h2">Anfrage</h2>
              <b-form-group id="group-pdf-btn" label-for="pdf-btn">
                <b-button
                  id="pdf-btn"
                  class="pdf-btn"
                  type="submit"
                  variant="primary"
                  v-on:click="exportPDF"
                  >PDF herunterladen</b-button
                >
              </b-form-group>

              <b-form-group
                id="input-group-1"
                class="full-width"
                label="Anrede:"
                label-for="input-1"
              >
                <b-form-select
                  id="input-1"
                  v-model="form.anrede"
                  :options="anrede"
                  required
                ></b-form-select>
              </b-form-group>

              <b-form-group
                id="input-group-2"
                label="Vorname:"
                label-for="input-2"
              >
                <b-form-input
                  id="input-2"
                  v-model="form.vname"
                  type="text"
                  required
                  placeholder="Vorname"
                ></b-form-input>
              </b-form-group>

              <b-form-group
                id="input-group-22"
                label="Nachname:"
                label-for="input-22"
              >
                <b-form-input
                  id="input-22"
                  v-model="form.name"
                  type="text"
                  required
                  placeholder="Nachname"
                ></b-form-input>
              </b-form-group>

              <b-form-group
                id="input-group-3"
                label="Unternehmen:"
                label-for="input-3"
              >
                <b-form-input
                  id="input-3"
                  v-model="form.firma"
                  type="text"
                  required
                  placeholder="Firmenname"
                ></b-form-input>
              </b-form-group>

              <b-form-group
                id="input-group-33"
                label="Abteilung:"
                label-for="input-33"
              >
                <b-form-input
                  id="input-33"
                  v-model="form.abteilung"
                  type="text"
                  required
                  placeholder="Abteilung"
                ></b-form-input>
              </b-form-group>

              <b-form-group
                id="input-group-4"
                class="full-width"
                label="Anschrift:"
                label-for="input-4"
              >
                <b-row>
                  <b-col cols="9">
                    <b-form-input
                      id="input-4"
                      v-model="form.strasse"
                      class="anschrift"
                      type="text"
                      required
                      placeholder="Strasse"
                    ></b-form-input>
                  </b-col>
                  <b-col cols="3">
                    <b-form-input
                      id="input-5"
                      class="anschrift"
                      v-model="form.hausnum"
                      type="text"
                      required
                      placeholder="Nr."
                    ></b-form-input>
                  </b-col>
                </b-row>
                <b-row>
                  <b-col cols="4">
                    <b-form-input
                      id="input-7"
                      class="anschrift"
                      v-model="form.plz"
                      type="number"
                      required
                      placeholder="Postleitzahl"
                    ></b-form-input>
                  </b-col>
                  <b-col cols="8">
                    <b-form-input
                      id="input-8"
                      class="anschrift"
                      v-model="form.ort"
                      type="text"
                      required
                      placeholder="Ort"
                    ></b-form-input>
                  </b-col>
                </b-row>
                <b-form-input
                  id="input-9"
                  class="anschrift"
                  v-model="form.land"
                  type="text"
                  required
                  placeholder="Land"
                ></b-form-input>
              </b-form-group>

              <b-form-group
                id="input-group-5"
                class="full-width"
                v-model="form.phone"
                label="Telefonnummer: "
                label-for="input-10"
                description="Ihre Telefonnummer wird ausschliesslich zur Bearbeitung Ihrer Anfrage verwendet"
              >
                <b-form-input
                  id="input-10"
                  v-model="form.phone"
                  type="text"
                  required
                  placeholder="Telefonnummer"
                ></b-form-input>
              </b-form-group>

              <b-form-group
                id="input-group-5"
                class="full-width"
                label="E-Mail-Adresse:"
                label-for="input-10"
                description="Ihre E-Mail wird ausschliesslich zur Bearbeitung Ihrer Anfrage verwendet"
              >
                <b-form-input
                  id="input-10"
                  v-model="form.email"
                  type="email"
                  required
                  placeholder="Email"
                ></b-form-input>
              </b-form-group>

              <b-form-group
                id="input-group-6"
                class="full-width"
                label="Ihre Nachricht:"
                label-for="input-11"
              >
                <b-form-textarea
                  id="input-11"
                  v-model="form.message"
                  placeholder="Hier ist Platz für Ihre Nachricht"
                  rows="3"
                  max-rows="6"
                ></b-form-textarea>
              </b-form-group>
              <b-row>
                <b-col cols="2">
                  <b-form-group id="group-submit-btn" label-for="submit">
                    <b-button
                      id="submit"
                      class="form-btn"
                      type="submit"
                      variant="primary"
                      >Abschicken</b-button
                    >
                  </b-form-group>
                </b-col>
              </b-row>
            </b-form>
          </div>
          <b-button id="zurueck" class="zurueck" v-on:click="zurueck"
            >zurück</b-button
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { EventBus } from "@/main.js";
import { doc } from "@/main.js";
import moment from "moment";
import jsPDF from "jspdf";
import "jspdf-autotable";

export default {
  data() {
    return {
      // Tabellenspalten: Referenzschlüssel, Spaltennamen und Sortierbarkeit
      fields: [
        {
          key: "anzahl",
          label: "Anzahl",
          sortable: true,
        },
        {
          key: "modultyp",
          label: "Modultyp",
          sortable: true,
        },
        {
          key: "höhe",
          label: "Höhe",
          sortable: true,
        },
        {
          key: "breite",
          label: "Breite",
          sortable: true,
        },
        {
          key: "länge",
          label: "Länge",
          sortable: true,
        },
        {
          key: "geländerhöhe",
          label: "Geländerhöhe",
          sortable: true,
        },
        {
          key: "laufstegbreite",
          label: "Laufstegbreite",
          sortable: true,
        },
        {
          key: "treppenbreite",
          label: "Treppenbreite",
          sortable: true,
        }
      ],
      // Items Template: jedes Item = 1 Zeile der Tabelle
      // über den Key werden die Spalten referenziert,
      // belegt werden die Items in der Funktion addNewRows
      // Aufgerufen wird diese Z: 292 in computed
      // Definiert ist sie in Z: 355
      items: [],
      moduleTypeTemplate: [
        "Modul 2",
        "Modul 3",
        "Modul 4",
        "Modul 7",
        "Modul 6"
      ],
      zusatzTreppe: ["Zusatztritt 1-stufig", "Zusatztritt 2-stufig"],
      heightcont: 0,
      widthcont: 0,
      lengthcont: 6.05,
      gelaender: 0,
      treppe: 0,
      laufsteg: 0,
      containertyp: "",
      containertypen: [],
      anzahlModule: [],
      anzModuleRechts: [],
      anzModuleLinks: [],
      width: 0,
      output: null,
      modulBezRechts: null,
      modulBezLinks: null,
      modBez6B: null,
      modBez6Lv: null,
      modBez6Lh: null,
      BueroAnz: null,
      LagerVAnz: null,
      LagerHAnz: null,
      modBez6Lv_2Stützen: null,
      LagerVAnz_2Stützen: null,

      form: {
        anrede: null,
        vname: "",
        name: "",
        firma: "",
        abteilung: "",
        phone: "",
        email: "",
        strasse: "",
        hausnum: "",
        plz: "",
        ort: "",
        land: "",
        checked: [],
        message: ""
      },
      anrede: [
        { text: "Bitte auswählen", value: null },
        "Frau",
        "Herr",
        "Divers"
      ],
      show: true
    };
  },
  mounted() {
    const self = this;
    EventBus.$on("p3righttop4", function({
      modulBezRechts,
      modulBezLinks,
      modBez6B,
      modBez6Lv,
      modBez6Lh,
      BueroAnz,
      LagerVAnz,
      LagerHAnz,
      gelaender,
      treppe,
      laufsteg,
      heightCont,
      widthCont,
      modBez6Lv_2Stützen,
      LagerVAnz_2Stützen,
      output
    }) {
      (self.modulBezRechts = modulBezRechts),
        (self.modulBezLinks = modulBezLinks),
        (self.modBez6B = modBez6B),
        (self.modBez6Lv = modBez6Lv),
        (self.modBez6Lh = modBez6Lh),
        (self.BueroAnz = BueroAnz),
        (self.LagerVAnz = LagerVAnz),
        (self.LagerHAnz = LagerHAnz),
        (self.gelaender = gelaender.toFixed(2)),
        (self.treppe = treppe.toFixed(2)),
        (self.laufsteg = laufsteg.toFixed(2)),
        (self.heightcont = heightCont.toFixed(2)),
        (self.widthcont = widthCont.toFixed(2)),
        (self.modBez6Lv_2Stützen = modBez6Lv_2Stützen),
        (self.LagerVAnz_2Stützen = LagerVAnz_2Stützen),
        (self.output = output),
        self.addNewRows();
    });
    if (self.modulBezRechts == null) {
      EventBus.$emit("p4rdy");
    }
  },

  methods: {
    // abschicken Funktion bei Button Klick: hier wird das Backend angebunden!
    onSubmit(evt) {
      evt.preventDefault();
      alert(JSON.stringify(this.form));
    },
    // zurück zur Vorseite/view
    zurueck: function() {
      EventBus.$emit("p4top3");
      this.anzLagervorne = 0;
      this.anzLagerhinten = 0;
      this.anzBüro = 0;
      this.items = [];
      for (var j = 0; j < 5; j++) {
        this.$set(this.modulBezeichnung, j, "");
      }
    },
    loadImage(url) {
      return new Promise((resolve) => {
        let img = new Image();
        img.onload = () => resolve(img);
        img.src = url;
      })
},
    /* Export der Daten als pdf. Funktioniert! Aber aktuell nicht auf den dynamischen Daten,
        das liegt aber an der Tabelle und dem Datenhandling nicht der PDF Download Funktion
        es können hiermit PDFs heruntergeladen werden.
       */
     
    exportPDF() {
      let vm = this;
      // let data = objects.map(obj => Object.values(obj.items))
      
      var header =
        "data:image/png;base64,";
      var footer =
        "data:image/png;base64,";
     
      
      //Datum erzeugen
      var date = moment().format("DD.MM.YYYY");
      doc.setFontSize(9);
      doc.text(date, 510, 160);
      doc.text(
        this.form.anrede + " " + this.form.vname + " " + this.form.name,
        50,
        180
      );
      doc.text(this.form.firma + " " + this.form.abteilung, 50, 190);
      doc.text(this.form.strasse + " " + this.form.hausnum, 50, 200);
      doc.text(this.form.plz + " " + this.form.ort, 50, 210);
      doc.text(this.form.land, 50, 220);
      doc.text("Tel.: " + this.form.phone, 50, 230);
      doc.text("Email: " + this.form.email, 50, 240);
      doc.text("Anmerkung: ", 50, 250);
      doc.text(this.form.message, 50, 260);

      doc.addImage(header, "PNG", 0, 0, 600, 139.86);
      doc.addImage(footer, "PNG", 0, 800, 600, 54.68);

      let columns = [
        // fixe Werte einer Beispieldatei
        { title: "Anzahl", dataKey: "anzahl" },
        { title: "Modultyp", dataKey: "modultyp" },
        { title: "Höhe", dataKey: "höhe" },
        { title: "Breite", dataKey: "breite" },
        { title: "Länge", dataKey: "länge" },
        { title: "Geländerhöhe", dataKey: "geländerhöhe" },
        { title: "Laufstegbreite", dataKey: "laufstegbreite" },
        { title: "Treppenbreite", dataKey: "treppenbreite" }
      ];
      doc.autoTable(columns, vm.items, {
        startY: 320,
        headStyles: {
          fillColor: [51, 67, 116]
        }
      });
      doc.save("Containertreppen Teileliste.pdf");
    },
    
    // Funktion zur dynamischen Erzeugung neuer Tabellenzeilen je nach Datensatz
    addNewRows: function() {
      const self = this;
      
      if (
        this.modulBezLinks == this.modulBezRechts &&
        this.modulBezRechts != ""
      ) {
        self.items.push({
          anzahl: 2,
          modultyp: self.modulBezRechts,
          höhe: self.heightcont,
          breite: self.widthcont,
          länge: self.lengthcont,
          geländerhöhe: self.gelaender,
          laufstegbreite: self.laufsteg,
          treppenbreite: self.treppe
        });
      } else {
        if (this.modulBezRechts != "") {
          self.items.push({
            anzahl: 1,
            modultyp: self.modulBezRechts,
            höhe: self.heightcont,
            breite: self.widthcont,
            länge: self.lengthcont,
            geländerhöhe: self.gelaender,
            laufstegbreite: self.laufsteg,
            treppenbreite: self.treppe
          });
        }
        if (this.modulBezLinks != "") {
          self.items.push({
            anzahl: 1,
            modultyp: self.modulBezLinks,
            höhe: self.heightcont,
            breite: self.widthcont,
            länge: self.lengthcont,
            geländerhöhe: self.gelaender,
            laufstegbreite: self.laufsteg,
            treppenbreite: self.treppe
          });
        }
      }
      if (self.BueroAnz > 0) {
        self.items.push({
          anzahl: self.BueroAnz,
          modultyp: self.modBez6B,
          höhe: self.heightcont,
          breite: self.widthcont,
          länge: self.lengthcont,
          geländerhöhe: self.gelaender,
          laufstegbreite: self.laufsteg,
          treppenbreite: self.treppe
        });
      }
      if (self.LagerHAnz > 0) {
        self.items.push({
          anzahl: self.LagerHAnz,
          modultyp: self.modBez6Lh,
          höhe: self.heightcont,
          breite: self.widthcont,
          länge: self.lengthcont,
          geländerhöhe: self.gelaender,
          laufstegbreite: self.laufsteg,
          treppenbreite: self.treppe
        });
      }
      if (self.LagerVAnz > 0) {
        self.items.push({
          anzahl: self.LagerVAnz,
          modultyp: self.modBez6Lv,
          höhe: self.heightcont,
          breite: self.widthcont,
          länge: self.lengthcont,
          geländerhöhe: self.gelaender,
          laufstegbreite: self.laufsteg,
          treppenbreite: self.treppe
        });
      }
      if (self.LagerVAnz_2Stützen > 0) {
        self.items.push({
          anzahl: self.LagerVAnz_2Stützen,
          modultyp: self.modBez6Lv_2Stützen,
          höhe: self.heightcont,
          breite: self.widthcont,
          länge: self.lengthcont,
          geländerhöhe: self.gelaender,
          laufstegbreite: self.laufsteg,
          treppenbreite: self.treppe
        });
      }
    }
  }
};
</script>

<style scoped>
* {
  box-sizing: border-box;
}

.body {
  background: whitesmoke;
  color: #003f84;
  line-height: 1.5;
  font-weight: normal;
}
.partslist{
    white-space: nowrap;
}
#right-wrapper {
    padding: 0;
  }
.container {
  max-width: 5000px;
  padding: 1em;
  border: 1px solid black;
}
#left {
  background: whitesmoke;
  padding-left: 0;
  width: 100%;
}

.out-list {
  background: #e6e7e8;
}

.out-list h2,
.out-list table {
  text-align: center;
  margin: 0 0 1rem 0;
}

.customer-input {
  padding-top: 0.625em;
  padding-bottom: 0.625em;
  padding-left: 0;
  background: #9c9d9f;
  width: 100%;
}

#right > * {
  padding: 0;
}

.form-btn {
  color: whitesmoke;
  background: #003f84;
  margin-right: 0;
  margin-left: 0;
  font-size: inherit;
  height: 35px;
  width: 100px;
}

/* FORM STYLES */
form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  margin-top: 30px;
  margin-right: 20px;
  margin-left: 20px;
}

#Anfrage-h2 {
  margin-top: 0.8em;
}

.full-width {
  grid-column: 1/3;
}

.pdf-btn {
  color: whitesmoke;
  background: #003f84;
  height: 65px;
  width: 120px;
  text-align: center;
  border: none;
  box-shadow: none;
}

.anschrift {
  margin: 5px;
}

#submit {
  background: #003f84;
  color: whitesmoke;
  border: 0;
}

#submit:hover {
  background: #92bde7;
  color: #000000;
  outline: 0;
  transition: background-color 1s ease-out;
}

.zurueck {
  margin: 1em;
  color: whitesmoke;
  background: #003f84;
  border: 0;
  float: right;
  width: 30%;
}

.zurueck:hover {
  background: #9c9d9f;
  color: #000000;
  outline: 0;
}

/*LARGE SCREENS*/
@media (min-width: 1200px) and (max-width: 5000px) {
  .wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .wrapper > * {
    padding: 2em;
  }

  .out-list {
    text-align: left;
  }
}
</style>
