<template>
  <div class="Hauptcontainer-3">
      <div class="angaben">
        <h6>Angabe Modulbreiten und Höhen:</h6>
        </div>
      <div class="gelaenderh">
        <b class="tag">Geländerhöhe:</b>
      </div>
        <div class="eins">
          <button class="btn btn-outline-secondary" v-on:click="setGelaender(1.00)" v-bind:class="{'activeBtn': gelaender == 1.00}">1.00</button>
          </div>
        <div class="einszehn">
          <button class="btn btn-outline-secondary" v-on:click="setGelaender(1.10)" v-bind:class="{'activeBtn': gelaender == 1.10}">1.10</button>
        </div>
        <div class="breitege">
          <b>Breite Laufsteg:</b>
        </div>
        <div class="einsnullacht">
          <button type="button" class="btn btn-outline-secondary" v-bind:class="{'activeBtn': laufsteg == 1.08 && lagervorhanden == false}">1.08</button>
        </div>
        <div class="breitetre">
          <b>Breite Treppen:</b>
        </div>
        <div class="eins_treppe">
          <button class="btn btn-outline-secondary" @click="setTreppe(1.00)" v-bind:class="{'activeBtn': treppe == 1.00}">1.00</button>
          </div>
        <div class="einszwei">
          <button class="btn btn-outline-secondary" @click="setTreppe(1.20)" v-bind:class="{'activeBtn': treppe == 1.20}">1.20</button>
        </div>
        <div class="dropdown-links">
          <!--Dropdown für linke Seite-->
          <b-dropdown style=width:90% id="dropdown-1" text="Modul Links" class="m-md-2">
            <b-dropdown-item @click="setModulesLeft(null)"
                             v-bind:class="{'activeBtn': modulesleft == null}">Keine Auswahl</b-dropdown-item>
            <div>
              <b-dropdown-item id="popover-2-links"
                               v-show="containerlinks == 'buerostirn' && height == 2 && rightActivated"
                               @click="setModulesLeft(2)"
                               v-bind:class="{'activeBtn': modulesleft == 2}">Modul 2</b-dropdown-item>
              <b-popover ref="popover"
                         target="popover-2-links"
                         triggers="hover"
                         placement="top">
                <img src="@/assets/CT-2-111-111_blau.png" width="200" height="114" />
              </b-popover>
            </div>
            <div>
              <b-dropdown-item id="popover-3-links"
                               v-show="containerlinks == 'buerostirn' && height == 2 && rightActivated"
                               @click="setModulesLeft(3)"
                               v-bind:class="{'activeBtn': modulesleft == 3}">Modul 3</b-dropdown-item>
              <b-popover ref="popover"
                         target="popover-3-links"
                         triggers="hover"
                         placement="top">
                <img src="@/assets/CT-3-111-110_blau.png" width="200" height="114" />
              </b-popover>
            </div>
            <b-dropdown-item v-show="containerlinks == 'buerolaengs'&& height == 2 && rightActivated"
                             @click="setModulesLeft(4)"
                             v-bind:class="{'activeBtn': modulesleft == 4}">Modul 4</b-dropdown-item>
            <b-dropdown-item v-show="AnzeigeModul7_links == true && rightActivated"
                             @click="setModulesLeft(7)"
                             v-bind:class="{'activeBtn': modulesleft == 7}">Modul 7</b-dropdown-item>
            <b-dropdown-divider></b-dropdown-divider>
          </b-dropdown>
        </div>
          <!--Dropdown für die Module der rechten Seite-->
        <div class="dropdown-rechts">
          <b-dropdown style=width:90% id="dropdown-2" text="Modul Rechts" class="m-md-2">
            <b-dropdown-item @click="setModulesRight(null)"
                             v-bind:class="{'activeBtn': modulesright == null}">Keine Auswahl</b-dropdown-item>
            <div>
              <b-dropdown-item id="popover-2-rechts"
                               v-show="containerrechts == 'buerostirn' && height == 2 && leftActivated"
                               @click="setModulesRight(2)"
                               v-bind:class="{'activeBtn': modulesright == 2}">Modul 2</b-dropdown-item>
              <b-popover ref="popover"
                         target="popover-2-rechts"
                         triggers="hover"
                         placement="top">
                <img src="@/assets/CT-2-111-111_blau.png" width="200" height="114" />
              </b-popover>
            </div>
            <div>
              <b-dropdown-item id="popover-3-rechts"
                               v-show="containerrechts == 'buerostirn'&& height == 2 && leftActivated"
                               @click="setModulesRight(3)"
                               v-bind:class="{'activeBtn': modulesright == 3}">Modul 3</b-dropdown-item>
              <b-popover ref="popover"
                         target="popover-3-rechts"
                         triggers="hover"
                         placement="top">
                <img src="@/assets/CT-3-111-110_blau.png" width="200" height="114" />
              </b-popover>
            </div>
            <b-dropdown-item v-show="containerrechts == 'buerolaengs' && height == 2 && leftActivated"
                             @click="setModulesRight(4)"
                             v-bind:class="{'activeBtn': modulesright == 4}">Modul 4</b-dropdown-item>
            <b-dropdown-item v-show="AnzeigeModul7_rechts == true && leftActivated"
                             @click="setModulesRight(7)"
                             v-bind:class="{'activeBtn': modulesright == 3}">Modul 7</b-dropdown-item>
            <b-dropdown-divider></b-dropdown-divider>
          </b-dropdown>
        </div>
    <div class="anfrage-mod1">
        <p>*Modul 1: Auf Anfrage</p>
      </div>
    <div class="weiter">
      <b-button v-on:click="weiter" id="weiter" class="navigation1">weiter</b-button>
    </div>
    <div class="zurueck">
      <b-button v-on:click="zurueck" id="zur�ck" class="navigation2">zurück</b-button>
    </div>
  </div>
</template>

<script>
import { EventBus } from '@/main.js'
export default {
  name: 'page3',
  data: function () {
    return {
      rightActivated: true,
      leftActivated: true,
      AnzeigeModul7_links: false,
      AnzeigeModul7_rechts: false,
      activateModules: false,
      containertyp: [],
      modulauswahl: [],
      gelaender: 1.00,
      treppe: 1.00,
      laufsteg: 1.08,
      lagervorhanden: false,
      buerostirn: false,
      buerolaengs: false,
      lagerhinten: false,
      lagervorne: false,
      modulesright: '',
      modulesleft: '',
      heightcont: 0,
      widthcont: 0,
      lengthcont: 0,
      anzModulRechts: 0,
      anzModulLinks: 0,
      height: 0,
      width: 0,
      container: [],
      containerlinks: null,
      containerrechts: null,
      modulBezRechts: '',
      modulBezLinks: '',
      modBez6B: 'start',
      modBez6Lv: 'start',
      modBez6Lh: 'start',
      BueroAnz: 0,
      LagerVAnz: 0,
      LagerHAnz: 0,
      LagerVAnz_2Stützen: 0,
      modBez6Lv_2Stützen: ''
    }
  },
  methods: {
    setTreppe (breite) {
      this.treppe = breite
      EventBus.$emit('setTreppe', this.treppe)
    },

    setGelaender (höhe) {
      this.gelaender = höhe
      EventBus.$emit('setGelaender', this.gelaender)
    },
    // führt die Auswahl der rechten Seite durch
    setModulesRight: function (id) {
      if (id == null) {
        this.modulesright = ''
      } else {
        this.modulesright = id
        if (this.width == 1 && this.leftActivated == true) {
          this.rightActivated = false
        }
      }
      EventBus.$emit('typeSelectedRight', id)
    },
    // führt die Auswahl der rechten Seite durch
    setModulesLeft: function (id) {
      if (id == null) {
        this.modulesleft = ''
      } else {
        this.modulesleft = id
        if (this.width == 1 && this.rightActivated == true) {
          this.leftActivated = false
        }
      }
      EventBus.$emit('typeSelectedLeft', id)
    },
    setRigthLeft () {
      if (this.height > 1) {
        this.containerlinks = this.containertyp[this.width + 8]
        this.containerrechts = this.containertyp[9]
      }
      if (this.height > 2) {
        if (this.containertyp[18] == 'buerostirn' && this.containertyp[9] == 'buerostirn') {
          this.AnzeigeModul7_links = true
        }
        if (this.containertyp[this.width + 8] == 'buerostirn' && this.containertyp[this.width + 17] == 'buerostirn') {
          this.AnzeigeModul7_rechts = true
        }
      }
    },
    modBez () {
      if (this.modulesright != '') {
        if (this.gelaender == 1.1) {
          this.modulBezRechts += 'CT-'
        } else {
          this.modulBezRechts += 'CTB-'
        }
        this.modulBezRechts += this.modulesright.toString()
        if (this.heightcont == 2.60) {
          this.modulBezRechts += '-1'
        } else if (this.heightcont == 2.80) {
          this.modulBezRechts += '-2'
        } else if (this.heightcont == 2.96) {
          this.modulBezRechts += '-4'
        }
        if (this.widthcont == 2.43) {
          this.modulBezRechts += '1'
        } else {
          this.modulBezRechts += '2'
        }
        if (this.containertyp[0] == 'buerostirn' || this.containertyp[0] == 'buerolaengs') {
          this.modulBezRechts += '1-';
        } else if (this.containertyp[0] == 'lagervorne') {
          this.modulBezRechts += '2-';
        } else {
          this.modulBezRechts += '3-';
        }
        if (this.treppe == 1.00) {
          this.modulBezRechts += '1'
        } else {
          this.modulBezRechts += '2'
        }
        if (this.laufsteg == 1.08) {
          this.modulBezRechts += '1'
        } else {
          this.modulBezRechts += '2'
        }
        if (this.modulesright == 7) {
          this.modulBezRechts += '4'
        } else if (this.modulesright == 2 || this.modulesright == 4 || this.containertyp[0] == 'lagervorne') {
          if (this.laufsteg == 1.08) {
            this.modulBezRechts += '1'
          } else {
            this.modulBezRechts += '2'
          }
        } else {
          this.modulBezRechts += '0'
        }
      } else if (this.modulesright == '' && this.modulesleft != 4 && this.width > 1) {
        if (this.containertyp[0] == 'buerostirn' || this.containertyp[0] == 'buerolaengs') {
          this.BueroAnz++
        } else if (this.containertyp[0] == 'lagervorne') {
          if (this.containertyp[1] == 'lagervorne') {
            this.LagerVAnz++
          } else {
            this.LagerVAnz_2Stützen++
          }
        } else if (this.containertyp[0] == 'lagerhinten'){
          this.LagerHAnz++
        }
      
        if (this.height > 2) {
          this.BueroAnz++
        }
        if ((this.containertyp[0] == 'buerostirn' || this.containertyp[0] == 'buerolaengs') && this.modBez6B == 'start') {
          if (this.gelaender == 1.1) {
            this.modBez6B = 'CT-6-'
          } else {
            this.modBez6B = 'CTB-6-'
          }
          this.modBez6B += '0'
          if (this.widthcont == 2.43) {
            this.modBez6B += '1'
          } else {
            this.modBez6B += '2'
          }
          this.modBez6B += '1-0'
          if (this.laufsteg == 1.08) {
            this.modBez6B += '1'
          } else {
            this.modBez6B += '2'
          }
          this.modBez6B += '0'
        } else if (this.containertyp[0] == 'lagerhinten' && this.modBez6Lh == 'start') {
          
          if (this.gelaender == 1.1) {
            this.modBez6Lh = 'CT-6-'
          } else {
            this.modBez6Lh = 'CTB-6-'
          }
          if (this.heightcont == 2.6) {
            this.modBez6Lh += '1'
          } else if (this.heightcont == 2.80) {
            this.modBez6Lh += '2'
          } else {
            this.modBez6Lh += '4'
          }
          if (this.widthcont == 2.43) {
            this.modBez6Lh += '1'
          } else {
            this.modBez6Lh += '2'
          }
          this.modBez6Lh += '3-0'
          if (this.laufsteg == 1.08) {
            this.modBez6Lh += '1'
          } else {
            this.modBez6Lh += '2'
          }
          this.modBez6Lh += '0'
        } else if (this.containertyp[0] == 'lagervorne' && this.modBez6Lv == 'start') {
          if (this.containertyp[1] == 'lagervorne') {
            if (this.gelaender == 1.1) {
              this.modBez6Lv = 'CT-6-'
            } else {
              this.modBez6Lv = 'CTB-6-'
            }
            if (this.heightcont == 2.6) {
              this.modBez6Lv += '1'
            } else if (this.heightcont == 2.80) {
              this.modBez6Lv += '2'
            } else {
              this.modBez6Lv += '4'
            }
            if (this.widthcont == 2.43) {
              this.modBez6Lv += '1'
            } else {
              this.modBez6Lv += '2'
            }
            this.modBez6Lv += '2-012'
          } else {
            if (this.gelaender == 1.1) {
              this.modBez6Lv_2Stützen += 'CT-6-'
            } else {
              this.modBez6Lv_2Stützen += 'CTB-6-'
            }
            if (this.heightcont == 2.6) {
              this.modBez6Lv_2Stützen += '1'
            } else if (this.heightcont == 2.80) {
              this.modBez6Lv_2Stützen += '2'
            } else {
              this.modBez6Lv_2Stützen += '4'
            }
            if (this.widthcont == 2.43) {
              this.modBez6Lv_2Stützen += '1'
            } else {
              this.modBez6Lv_2Stützen += '2'
            }
            this.modBez6Lv_2Stützen += '2-011'
          }
        }
      }
      
      if (this.modulesleft != '') {
        if (this.gelaender == 1.1) {
          this.modulBezLinks += 'CT-'
        } else {
          this.modulBezLinks += 'CTB-'
        }
        this.modulBezLinks += this.modulesleft.toString()
        if (this.heightcont == 2.6) {
          this.modulBezLinks += '-1'
        } else if (this.heightcont == 2.80) {
          this.modulBezLinks += '-2'
        } else if (this.heightcont == 2.96) {
          this.modulBezLinks += '-4'
        }
        if (this.widthcont == 2.43) {
          this.modulBezLinks += '1'
        } else {
          this.modulBezLinks += '2'
        }
        if (this.containertyp[this.width - 1] == 'buerostirn' || this.containertyp[this.width - 1] == 'buerolaengs') {
          this.modulBezLinks += '1-'
        } else if (this.containertyp[this.width - 1] == 'lagervorne') {
          this.modulBezLinks += '2-'
        } else {
          this.modulBezLinks += '3-'
        }
        if (this.treppe == 1.00) {
          this.modulBezLinks += '1'
        } else {
          this.modulBezLinks += '2'
        }
        if (this.laufsteg == 1.08) {
          this.modulBezLinks += '1'
        } else {
          this.modulBezLinks += '2'
        }
        if (this.modulesleft == 7) {
          this.modulBezLinks += '4'
        } else if (this.modulesleft == 2 || this.modulesleft == 4 || this.containertyp[this.width - 1] == 'lagerhinten') {
          if (this.laufsteg == 1.08) {
            this.modulBezLinks += '1'
          } else {
            this.modulBezLinks += '2'
          }
        } else {
          this.modulBezLinks += '0'
        }
        
      } else if (this.modulesleft == '' && this.modulesright != 4 && this.width > 1) {
        if (this.containertyp[this.width - 1] == 'buerostirn' || this.containertyp[this.width - 1] == 'buerolaengs') {
          this.BueroAnz++
        } else if (this.containertyp[this.width - 1] == 'lagervorne') {
          this.LagerVAnz_2Stützen++
        } else if (this.containertyp[this.width - 1] == 'lagerhinten') {
          this.LagerHAnz++
        }
        console.log(this.LagerHAnz, this.BueroAnz, this.LagerVAnz_2Stützen)
        if (this.height > 2) {
          this.BueroAnz++
        }
        if ((this.containertyp[this.width - 1] == 'buerostirn' || this.containertyp[this.width - 1] == 'buerolaengs') && this.modBez6B == 'start') {
          if (this.gelaender == 1.1) {
            this.modBez6B = 'CT-6-'
          } else {
            this.modBez6B = 'CTB-6-'
          }
          this.modBez6B += '0'
          if (this.widthcont == 2.43) {
            this.modBez6B += '1'
          } else {
            this.modBez6B += '2'
          }
          this.modBez6B += '1-0'
          if (this.laufsteg == 1.08) {
            this.modBez6B += '1'
          } else {
            this.modBez6B += '2'
          }
          this.modBez6B += '0'
        } else if (this.containertyp[this.width - 1] == 'lagerhinten' && this.modBez6Lh == 'start') {
          if (this.gelaender == 1.1) {
            this.modBez6Lh = 'CT-6-'
          } else {
            this.modBez6Lh = 'CTB-6-'
          }
          this.modBez6Lh += '0'
          if (this.widthcont == 2.43) {
            this.modBez6Lh += '1'
          } else {
            this.modBez6Lh += '2'
          }
          this.modBez6Lh += '3-0'
          if (this.laufsteg == 1.08) {
            this.modBez6Lh += '1'
          } else {
            this.modBez6Lh += '2'
          }
          this.modBez6Lh += '0'
        } else if (this.containertyp[this.width - 1] == 'lagervorne' && this.modBez6Lv_2Stützen == '') {
          if (this.gelaender == 1.1) {
            this.modBez6Lv_2Stützen += 'CT-6-'
          } else {
            this.modBez6Lv_2Stützen += 'CTB-6-'
          }
          
          if (this.heightcont == 2.6) {
            this.modBez6Lv_2Stützen += '1'
          } else if (this.heightcont == 2.80) {
            this.modBez6Lv_2Stützen += '2'
          } else {
            this.modBez6Lv_2Stützen += '4'
          }
          if (this.widthcont == 2.43) {
            this.modBez6Lv_2Stützen += '1'
          } else {
            this.modBez6Lv_2Stützen += '2'
          }
          this.modBez6Lv_2Stützen += '2-011'

        }
      }
     
    },
    mod6 () {
      for (var i = 1; i < this.width - 1; i++) {
        // Legt die Anzahl der Module 6 fest
        if (this.containertyp[i] == 'buerostirn' || this.containertyp[i] == 'buerolaengs') {
          this.BueroAnz++
        } else if (this.containertyp[i] == 'lagerhinten') {
          this.LagerHAnz++
        } else if (this.containertyp[i] == 'lagervorne') {
          if (this.containertyp[i + 1] == 'lagervorne') {
            this.LagerVAnz++
          } else {
            this.LagerVAnz_2Stützen++
          }
        }
       
        if (this.height > 2) {
          this.BueroAnz++
        }
       
        // Legt die Modulbezeichnungen für 6 fest
        if ((this.containertyp[i] == 'buerostirn' || this.containertyp[i] == 'buerolaengs') && this.modBez6B == 'start') {
          
          if (this.gelaender == 1.1) {
            this.modBez6B = 'CT-6-'
          } else {
            this.modBez6B = 'CTB-6-'
          }
          // Höhe
          this.modBez6B += '0'
          //Breite
          if (this.widthcont == 2.43) {
            this.modBez6B += '1'
          } else {
            this.modBez6B += '2'
          }
          
         
          //if (this.widthcont == 2.43) {
            //this.modBez6B += '1'
          //} else {
            //this.modBez6B += '2'
          //}
          this.modBez6B += '1-0'
         
          if (this.laufsteg == 1.08) {
            this.modBez6B += '1'
          } else {
            this.modBez6B += '2'
          }
          this.modBez6B += '0'
         
        } else if (this.containertyp[i] == 'lagerhinten' && this.modBez6Lh == 'start') {
         
          if (this.gelaender == 1.1) {
            this.modBez6Lh = 'CT-6-'
          } else {
            this.modBez6Lh = 'CTB-6-'
          }
          // Höhe
          this.modBez6Lh += '0'
          //Breite
          if (this.widthcont == 2.43) {
            this.modBez6Lh += '1'
          } else {
            this.modBez6Lh += '2'
          }
          
          this.modBez6Lh += '3-0'
          
          if (this.laufsteg == 1.08) {
            this.modBez6Lh += '1'
          } else {
            this.modBez6Lh += '2'
          }
          this.modBez6Lh += '0'
        } else if (this.containertyp[i] == 'lagervorne' && this.modBez6Lv == 'start') {
         
          if (this.modBez6Lv == '' && this.LagerVAnz != 0) {
            if (this.gelaender == 1.1) {
              this.modBez6Lv = 'CT-6-'
            } else {
              this.modBez6Lv = 'CTB-6-'
            }
            //Höhe
            this.modBez6Lv += '1'
            //Breite
            if (this.widthcont == 2.43) {
            this.modBez6Lv += '1'
          } else {
            this.modBez6Lv += '2'
          }
            this.modBez6Lv += '2-0'
            
            this.modBez6Lv += '12'
          }
          if (this.modBez6Lv_2Stützen == '' && this.LagerVAnz_2Stützen != 0) {
            if (this.gelaender == 1.1) {
              this.modBez6Lv_2Stützen += 'CT-6-'
            } else {
              this.modBez6Lv_2Stützen += 'CTB-6-'
            }
            this.modBez6Lv_2Stützen += '1'
            if (this.widthcont == 2.43) {
              this.modBez6Lv_2Stützen += '1'
            } else {
              this.modBez6Lv_2Stützen += '2'
            }
            
            this.modBez6Lv_2Stützen += '2-0'
            this.modBez6Lv_2Stützen += '11'
          }
        }
      }
      if (this.height > 2 && this.modBez6B == '') {
        
        if (this.gelaender == 1.1) {
          this.modBez6B += 'CT-6-'
        } else {
          this.modBez6B += 'CTB-6-'
        }
        this.modBez6B += '0'
        if (this.widthcont == 2.43) {
          this.modBez6B += '1'
        } else {
          this.modBez6B += '2'
        }
        this.modBez6B += '1-0'
        if (this.laufsteg == 1.08) {
          this.modBez6B += '1'
        } else {
          this.modBez6B += '2'
        }
        this.modBez6B += '0'
      }
    },
    // regelt den Wechsel zur nächsten Seite
    weiter: function () {
      if (this.modulesright == '' && this.modulesleft == '' && this.height > 1) {
        alert('Es wurde kein Treppenmodul ausgewählt.')
      } else {
        this.BueroAnz = 0,
        this.LagerVAnz = 0,
        this.LagerHAnz = 0,
        this.modulBezRechts = '',
        this.modulBezLinks = '',
        this.modBez6B = 'start',
        this.modBez6Lv = 'start',
        this.modBez6Lh = 'start',
        this.LagerVAnz_2Stützen = 0,
        this.modBez6Lv_2Stützen = ''
        this.modBez()
        if ((this.modulesright == 4 && this.modulesleft == '') ||
            (this.modulesright == '' && this.modulesleft == 4) ||
            (this.modulesright == 4 && this.modulesleft == 4)

        ) { } else {
          
          this.mod6()
        }
        // Seitenwechsel
        EventBus.$emit('p3top4', {
          modulBezRechts: this.modulBezRechts,
          modulBezLinks: this.modulBezLinks,
          modBez6B: this.modBez6B,
          modBez6Lv: this.modBez6Lv,
          modBez6Lh: this.modBez6Lh,
          BueroAnz: this.BueroAnz,
          LagerVAnz: this.LagerVAnz,
          LagerHAnz: this.LagerHAnz,
          gelaender: this.gelaender,
          treppe: this.treppe,
          laufsteg: this.laufsteg,
          modBez6Lv_2Stützen: this.modBez6Lv_2Stützen,
          LagerVAnz_2Stützen: this.LagerVAnz_2Stützen
        })
      }
    },
    // Wechsel zur vorherigen Seite: Alle Werte werden zurückgesetzt
    zurueck: function () {
      EventBus.$emit('p3top2')
    },
},
  computed: {
    btnStates () {
      return this.buttons.map(btn => btn.state)
    }
  },

  mounted () {
    const self = this
    EventBus.$on('appToP3rigth', function ({ width, heigth, heigthCont, widthCont, containertyp }) {
      self.width = width,
      self.height = heigth,
      self.heightcont = heigthCont,
      self.widthcont = widthCont,
      self.containertyp = containertyp
      self.setRigthLeft()
    })
    EventBus.$on('ChangesRightSide', function ({ heightCont, widthCont }) {
      self.heightcont = heightCont,
      self.widthcont = widthCont
    })
    EventBus.$emit('p3rigthrdy')
  }
}
</script>

