      this.scene = new BABYLON.Scene(this.engine);
      this.engine.runRenderLoop(function () {
        _this.scene.render();
      });
      BABYLON.DefaultLoadingScreen.prototype.displayLoadingUI = function () {
        if (document.getElementById("customLoadingScreenDiv")) {
          document.getElementById("customLoadingScreenDiv").style.display =
            "initial";
          // Do not add a loading screen if there is already one
          return;
        }

        this._loadingDiv = document.createElement("div");
        this._loadingDiv.id = "customLoadingScreen2";
        this._loadingDiv.innerHTML =
          "<img src='https://www.mauderer.de/static/ctc/loading2.gif'/>";
        var customLoadingScreenCss = document.createElement("style");
        customLoadingScreenCss.type = "text/css";
        customLoadingScreenCss.innerHTML = `
    #customLoadingScreen2{
        background-color: green;
        height: 100%;
        width: 100%;
        display: flex;
    }
    `;
        document
          .getElementsByTagName("head")[0]
          .appendChild(customLoadingScreenCss);
        this._resizeLoadingUI();
        window.addEventListener("resize", this._resizeLoadingUI);
        document.body.appendChild(this._loadingDiv);
      };

      BABYLON.DefaultLoadingScreen.prototype.hideLoadingUI = function () {
        document.getElementById("customLoadingScreenDiv").style.display =
          "none";
      };

      this.engine.displayLoadingUI();
      this.mainCamera = new BABYLON.ArcRotateCamera(
        "Camera",
        0,
        0,
        10,
        new BABYLON.Vector3(0, 0, 0),
        this.scene
      );
      this.mainCamera.attachControl(this.canvas, false);
      if (this.width < 3) {
        this.mainCamera.setPosition(
          new BABYLON.Vector3(-6, 2.56 * (this.height / 2), 9)
        );
      } else if (this.width < 8) {
        this.mainCamera.setPosition(
          new BABYLON.Vector3(
            -4 * ((this.width - 1) / 2),
            2.56 * (this.height / 2),
            12
          )
        );
