    setContainertype(index, containertyp) {
      const _this = this;
      var x, y;
      if (index >= 18) {
        y = 2;
        x = index - 18;
      } else if (index >= 9) {
        y = 1;
        x = index - 9;
      } else {
        y = 0;
        x = index;
      }
