      this.mod6geladen = false;
      for (var m = 0; m < 14; m++) {
        if (this.meshesmodsechs[m] != null) {
          this.meshesmodsechs[m].dispose();
        }
      }
      var i = 0;
      if (!(this.modulesright == 4 && this.modulesleft == null)) {
        while (this.meshesModRechts[i] != null) {
