  // Datenübergabe von Komponente Page2Rechts (Module, etc.)
  mounted() {
    const self = this;
    EventBus.$on('p3top4', function ({ modulBezRechts, modulBezLinks, modBez6B, modBez6Lv, BueroAnz,
      LagerVAnz, gelaender, treppe, laufsteg, modBez6Lv_2Stützen, LagerVAnz_2Stützen }) {
      EventBus.$emit("image", {output: self.output})
    })
    EventBus.$on("setTreppe", function (treppe) {
      self.treppe = treppe;
    });
    EventBus.$on("setLaufsteg", function (laufsteg) {
      self.laufsteg = laufsteg;
    });
    EventBus.$on("setGelaender", function (gelaender) {
      self.gelaender = gelaender;
    });
    EventBus.$on("appToP3left", function ({ width, heigth, containertyp }) {
      (self.width = width), (self.height = heigth);
      for (var i = 0; i < 27; i++) {
        self.$set(self.containertyp, i, containertyp[i]);
      }
    });

    EventBus.$on("typeSelectedRight", function (id) {
      self.engine.displayLoadingUI();

      var destroyedCalled = false;
      if (self.modulesright != null) {
        self.destroyMeshesModRechts();
      }
      self.modulesright = id;
      if (
        (self.modulesright == null &&
          (self.modulesleft == 6 || self.modulesleft == null)) ||
        (self.modulesright == null && self.modulesleft == 4)
      ) {
        self.destroyMod6();
        destroyedCalled = true;
      } else if (
        self.mod6geladen == false &&
        self.modulesright != 4 &&
        self.modulesright != null
      ) {
        self.AnzeigeMod6();
      }
      if (destroyedCalled == false) {
        self.AnzeigeModule("rechts");
      }
      self.assetsManager.onFinish = function(task){
          self.printDiv()
        }
      self.assetsManager.load();
    }),
      EventBus.$on("typeSelectedLeft", function (id) {
        self.engine.displayLoadingUI();
        var destroyedCalled = false;
        if (self.modulesleft != null) {
          self.destroyMeshesModLinks();
        }
        self.modulesleft = id;
        if (
          ((self.modulesright == 6 || self.modulesright == null) &&
            self.modulesleft == null) ||
          (self.modulesleft == null && self.modulesright == 4)
        ) {
          self.destroyMod6();
          destroyedCalled = true;
        } else if (
          self.mod6geladen == false &&
          self.modulesleft != 4 &&
