// Import aller Bibliotheken und Frameworks die in der App benötigt werden
import '@babel/polyfill'
import 'mutationobserver-shim'

import Vue from 'vue'
import './plugins/bootstrap-vue'
import BootstrapVue from 'bootstrap-vue'

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

import VueMq from 'vue-mq'

import App from './App.vue'
import router from './router'
import store from './store'
import VueHtml2Canvas from 'vue-html2canvas';

import vb from 'vue-babylonjs'
import * as BABYLON from 'babylonjs'
import 'babylonjs-loaders'
import jsPDF from "jspdf";
Vue.use(VueHtml2Canvas);
Vue.use(vb)

/* Steuert das Mobile first responsive design, funktioniert aktuell nur auf der Page 4
  Testballon den wir wegen fehlendem Nutzen nicht weiter verfolgt haben aber als praktische
  Demo für den Kunden belassen habenn
 */
export const doc = new jsPDF("p", "pt")
export const EventBus = new Vue()
Vue.use(BootstrapVue)
Vue.use(VueMq, {
  breakpoints: {
    sm: 450,
    md: 1250,
    lg: Infinity
  },
  defaultBreakpoint: 'md'
})

Vue.config.productionTip = false

// Erzeugt die Root Vue Instanz, an der die ganze App 'hängt'
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
