                !(index > 9 && index < 8 + _this.width)
              ) {
                task.loadedMeshes[k].actionManager = new BABYLON.ActionManager(
                  _this.scene
                )
                task.loadedMeshes[k].actionManager.registerAction(
                  new BABYLON.ExecuteCodeAction(
                    BABYLON.ActionManager.OnPickTrigger,
                    function () {
                      _this.click(index)
                    }
                  )
                )
                task.loadedMeshes[0].actionManager.registerAction(
                  new BABYLON.ExecuteCodeAction(
                    BABYLON.ActionManager.OnPointerOverTrigger,
                    function () {
                      _this.changeColorTrigger(index, true)
                    }
                  )
                )
                task.loadedMeshes[0].actionManager.registerAction(
                  new BABYLON.ExecuteCodeAction(
                    BABYLON.ActionManager.OnPointerOutTrigger,
                    function () {
                      if (_this.clicked[index] == false) {
                        _this.changeColorTrigger(index, false)
                      }
                    }
                  )
                )
              }
            } else {
              _this.$set(_this.loadedMesh, k + index * 11, null)
            }
          }
        }
      }
      if (containertyp == 'lagerhinten') {
        this.tasks[index] = this.assetsManager.addMeshTask(
          'task' + index,
          '',
          'https://www.mauderer.de/static/ctc/Lager/',
          'LagercontainerhintenHC2600BC2430.obj'
        )
        this.tasks[index].onSuccess = function (task) {
          var j = 0
          task.loadedMeshes[0].material = _this.colorContainer
          while (task.loadedMeshes[j] != null) {
            task.loadedMeshes[j].position = new BABYLON.Vector3(
              2.43 * x + 0.05,
              2.6 * y,
              0
            )
            j++
          }
          for (var k = 0; k < 11; k++) {
            if (task.loadedMeshes[k] != null) {
              _this.$set(_this.loadedMesh, k + index * 11, task.loadedMeshes[k])
              if (
                y != 3 &&
                !(index > 9 && index < 8 + _this.width)
              ) {
                task.loadedMeshes[k].actionManager = new BABYLON.ActionManager(
                  _this.scene
                )
                task.loadedMeshes[k].actionManager.registerAction(
                  new BABYLON.ExecuteCodeAction(
                    BABYLON.ActionManager.OnPickTrigger,
                    function () {
                      _this.click(index)
                    }
                  )
                )
                task.loadedMeshes[0].actionManager.registerAction(
                  new BABYLON.ExecuteCodeAction(
                    BABYLON.ActionManager.OnPointerOverTrigger,
                    function () {
                      _this.changeColorTrigger(index, true)
                    }
                  )
                )
                task.loadedMeshes[0].actionManager.registerAction(
                  new BABYLON.ExecuteCodeAction(
                    BABYLON.ActionManager.OnPointerOutTrigger,
                    function () {
                      if (_this.clicked[index] == false) {
                        _this.changeColorTrigger(index, false)
                      }
                    }
                  )
                )
              }
            } else {
              _this.$set(_this.loadedMesh, k + index * 11, null)
            }
          }
        }
      }
      if (containertyp == 'buerostirn') {
        this.tasks[index] = this.assetsManager.addMeshTask(
          'task' + index,
          '',
          'https://www.mauderer.de/static/ctc/BueroStirn/',
          'BuerostirnseitigHC2600BC2430.obj'
        )
        this.tasks[index].onSuccess = function (task) {
          var j = 0
          task.loadedMeshes[0].material = _this.colorContainer
          task.loadedMeshes[1].material = _this.colorContainer

          while (task.loadedMeshes[j] != null) {
            task.loadedMeshes[j].position = new BABYLON.Vector3(
              2.43 * x,
              2.6 * y,
              0
            )
            j++
          }

          for (var k = 0; k < 11; k++) {
            if (task.loadedMeshes[k] != null) {
              _this.$set(_this.loadedMesh, k + index * 11, task.loadedMeshes[k])
              if (
                y != 3 &&
                !(index > 9 && index < 8 + _this.width)
              ) {
                task.loadedMeshes[k].actionManager = new BABYLON.ActionManager(
                  _this.scene
                )
                task.loadedMeshes[k].actionManager.registerAction(
                  new BABYLON.ExecuteCodeAction(
                    BABYLON.ActionManager.OnPickTrigger,
                    function () {
                      _this.click(index)
                    }
                  )
                )
                task.loadedMeshes[0].actionManager.registerAction(
                  new BABYLON.ExecuteCodeAction(
                    BABYLON.ActionManager.OnPointerOverTrigger,
                    function () {
                      _this.changeColorTrigger(index, true)
                    }
                  )
                )
                task.loadedMeshes[0].actionManager.registerAction(
                  new BABYLON.ExecuteCodeAction(
                    BABYLON.ActionManager.OnPointerOutTrigger,
                    function () {
                      if (_this.clicked[index] == false) {
                        _this.changeColorTrigger(index, false)
                      }
                    }
                  )
                )
              }
            } else {
              _this.$set(_this.loadedMesh, k + index * 11, null)
            }
          }
        }
      }
      if (containertyp == 'buerolaengs') {
        this.tasks[index] = this.assetsManager.addMeshTask(
          'task' + index,
          '',
          'https://www.mauderer.de/static/ctc/BueroLaengs/',
          'BuerolaengsseitigHC2600BC2430.obj'
        )
        this.tasks[index].onSuccess = function (task) {
          var j = 0
          task.loadedMeshes[0].material = _this.colorContainer
          task.loadedMeshes[1].material = _this.colorContainer
          task.loadedMeshes[2].material = _this.colorContainer
          task.loadedMeshes[4].material = _this.colorContainer
          task.loadedMeshes[5].material = _this.colorContainer
          task.loadedMeshes[7].material = _this.colorContainer
          task.loadedMeshes[8].material = _this.colorContainer
          task.loadedMeshes[9].material = _this.colorContainer

          while (task.loadedMeshes[j] != null) {
            task.loadedMeshes[j].position = new BABYLON.Vector3(
              2.43 * x,
              2.6 * y,
              0
            )
            j++
          }

          for (var k = 0; k < 11; k++) {
            if (task.loadedMeshes[k] != null) {
              _this.$set(_this.loadedMesh, k + index * 11, task.loadedMeshes[k])
              if (
                y != 3 &&
                !(index > 9 && index < 8 + _this.width)
              ) {
                task.loadedMeshes[k].actionManager = new BABYLON.ActionManager(
                  _this.scene
                )
                task.loadedMeshes[k].actionManager.registerAction(
                  new BABYLON.ExecuteCodeAction(
                    BABYLON.ActionManager.OnPickTrigger,
                    function () {
                      _this.click(index)
                    }
                  )
                )
                task.loadedMeshes[0].actionManager.registerAction(
                  new BABYLON.ExecuteCodeAction(
                    BABYLON.ActionManager.OnPointerOverTrigger,
                    function () {
                      _this.changeColorTrigger(index, true)
                    }
                  )
                )
                task.loadedMeshes[0].actionManager.registerAction(
                  new BABYLON.ExecuteCodeAction(
                    BABYLON.ActionManager.OnPointerOutTrigger,
                    function () {
                      if (_this.clicked[index] == false) {
                        _this.changeColorTrigger(index, false)
                      }
                    }
                  )
                )
              }
            } else {
              _this.$set(_this.loadedMesh, k + index * 11, null)
            }
          }
        }
      }
      if (this.calledByUpdateDimension == false) {
        this.assetsManager.load()
      }
    },
    // Speichern des aktuellen Indexes des geklickten Buttons im 3x9 Feld
    click (number) {
      this.$set(this.clicked, number, !this.clicked[number])
      if (this.clicked[number] == true) {
        this.selectedCont.push(number)
        this.loadedMesh[number * 11].material = this.color
      } else {
        this.selectedCont.splice(this.selectedCont.indexOf(number), 1)
        this.loadedMesh[number * 11].material = this.color
      }

      // Datenübergabe nach P2 rechts
      EventBus.$emit('containerclicked1', {
        id: this.selectedCont
      })
    },
    // Vue-Wechsel auf P4, beim klicken auf den individuelle Anfrage Button
    inAnfrage: function () {
      EventBus.$emit('p3top4')
    }
  },
  created () {
    // Containertyp wird zu beginn standartisiert auf buerostirn gesetzt
    for (var index = 0; index < 27; index++) {
      this.containertypArr[index] = 'buerostirn'
    }
  },
  mounted () {
    const self = this
    for (var z = 0; z < 27; z++) {
      this.$set(this.clicked, z, false)
    }
    // Empfang, sobald User auf P2 Rechts Containertyp auswählt
    EventBus.$on('containertypchosen', function ({ containertyp }) {
      self.engine.displayLoadingUI()
      var k = 0
      self.calledByUpdateDimension = true
      while (self.selectedCont[k] != null) {
        self.$set(self.containertypArr, self.selectedCont[k], containertyp)
        self.setContainertype(self.selectedCont[k], containertyp)
        k++
      }
      self.assetsManager.load()
      // self.assetsManager.onFinish();

      self.calledByUpdateDimension = false
      self.selectedCont = []

      EventBus.$emit('containertypUpdate', {
        containertypArr: self.containertypArr
      })
    })
    // Datenempfang von P1 rechts
    EventBus.$on('p1top2left', function ({ heigth, width }) {
      self.width = width
      self.heigth = heigth
      self.init()
    })
    EventBus.$emit('p2leftrdy')
  }
}
</script>

