    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 0.4fr 3fr;
    height: 100%;

}
.textblock-2 {
    margin-left: 5%;
    margin-right: 5%;
    margin-top: 2.2%;
    grid-column: 1 / 2;
    grid-row: 1/ 2;

}
.rendercanvas-2 {
    grid-column: 1 / 2;
    grid-row: 1 / 3;
}
#renderCanvas{
  height: 100%;
}
