<template>
  <div class="container1">
      <div class="text-wrapper" style="white-space:pre-wrap;">
        {{ input }}
      </div>
      <b-button class="button-weiter" v-if="heigth != 0" v-on:click="weiter"
        >weiter</b-button
      >
  </div>
</template>

<script>
import { EventBus } from '@/main.js'
export default {
  data () {
    return {
      heigth: 0,
      width: 0,
      input:
        '\n' +
        'In wenigen Schritten zu Ihrer Containertreppen-Anlage\n' +
        '\n' +
        'Bitte treffen Sie Ihre Auswahl und bestätigen unten rechts mit weiter. Wir erstellen Ihnen umgehend eine passende Containertreppen-Anlage. \n' +
        'Sie wünschen Extras? Kein Problem, bitte senden Sie uns über unser Anfrageformular eine Nachricht. \n'
    }
  },
  // ganz normale funktionen die Aktiv aufgerufen werden müssen (z.B. hier durch einen Button click)
  methods: {
    // Regelt den VIEW Wechsel von Page1 zu Page2 sowe die Datenübergabe
    weiter: function () {
      EventBus.$emit('p1top2', { heigth: this.heigth, width: this.width })
    }
  },
  mounted () {
    // Empfängt die Angaben von P1 Links
    const self = this
    EventBus.$on('emited', function ({ heigth, width }) {
      self.heigth = heigth
      self.width = width
    })
  }
}
</script>

<style scoped>
  .text-wrapper {
      grid-column: 1 / 4;
      grid-row: 1 / 2;

  }
.container1 {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: 9fr 1fr;
  overflow: hidden;
  position: relative;
  padding-top: 5%;
  padding-left: 5%;
  padding-right: 5%;
  padding-bottom: 0%;
  height: 100%;
}
.button-weiter {
  grid-row: 2 / 3;
  grid-column: 3 / 4;
  width: 90%;
  height: 40px;
  color: whitesmoke;
  background-color: #003f84;
}
@media only screen and (max-width: 1150px) {
    .container1 {
        padding-top: 0;
        grid-template-rows: 5fr 1fr;
    }
}
</style>
